@font-face {
    font-family:'Baumans';
    src: url('../fonts/Baumans.eot');
	src: url('../fonts/Baumans.eot?#iefix') format('embedded-opentype'),
		url('../fonts/Baumans.woff2') format('woff2'),
		url('../fonts/Baumans.woff') format('woff'),
		url('../fonts/Baumans.svg#Baumans') format('svg');
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    unicode-range: U+0020-2074;
}